import { Breadcrumb } from "antd";
import React from "react";
import { useLocation } from "react-router-dom";

export default function AutoMLBreadcrumbs() {
  let location = useLocation();
  let link = location.pathname.split("/");
  let project = JSON.parse(localStorage.getItem("Project"));
  let paths = null;
  if (link.includes("customised_dataset")) {
    paths = [
      { name: "projects", link: "/beyond-data/automl/projects/" },
      {
        name: "models",
        link: `/beyond-data/automl/projects/${project.name}/models`,
      },
      {
        name: "select datasets",
        link: `/beyond-data/automl/projects/${project.name}/models/$modelname$/select_datasets`,
      },
      {
        name: "selected datasets",
        link: `/beyond-data/automl/projects/${project.name}/models/$modelname$/selected_datasets`,
      },
      {
        name: "dataset_processing",
        link: `/beyond-data/automl/projects/${project.name}/models/$modelname$/dataset_processing`,
      },
      {
        name: "link columns",
        link: `/beyond-data/automl/projects/${project.name}/models/$modelname$/link_columns`,
      },
      {
        name: "customised dataset",
        link: `#`,
      },
    ];
  }
  if (link.includes("link_columns")) {
    paths = [
      { name: "projects", link: "/beyond-data/automl/projects/" },
      {
        name: "models",
        link: `/beyond-data/automl/projects/${project.name}/models`,
      },
      {
        name: "select datasets",
        link: `/beyond-data/automl/projects/${project.name}/models/$modelname$/select_datasets`,
      },
      {
        name: "selected datasets",
        link: `/beyond-data/automl/projects/${project.name}/models/$modelname$/selected_datasets`,
      },
      {
        name: "dataset_processing",
        link: `/beyond-data/automl/projects/${project.name}/models/$modelname$/dataset_processing`,
      },
      {
        name: "link columns",
        link: `#`,
      },
    ];
  } else if (link.includes("dataset_processing")) {
    paths = [
      { name: "projects", link: "/beyond-data/automl/projects/" },
      {
        name: "models",
        link: `/beyond-data/automl/projects/${project.name}/models`,
      },
      {
        name: "select datasets",
        link: `/beyond-data/automl/projects/${project.name}/models/$modelname$/select_datasets`,
      },
      {
        name: "selected datasets",
        link: `/beyond-data/automl/projects/${project.name}/models/$modelname$/selected_datasets`,
      },
      {
        name: "dataset_processing",
        link: `#`,
      },
    ];
  } else if (link.includes("selected_datasets")) {
    paths = [
      { name: "projects", link: "/beyond-data/automl/projects/" },
      {
        name: "models",
        link: `/beyond-data/automl/projects/${project.name}/models`,
      },
      {
        name: "select datasets",
        link: `/beyond-data/automl/projects/${project.name}/models/$modelname$/select_datasets`,
      },
      {
        name: "selected datasets",
        link: `#`,
      },
    ];
  } else if (link.includes("select_datasets")) {
    paths = [
      { name: "projects", link: "/beyond-data/automl/projects/" },
      {
        name: "models",
        link: `/beyond-data/automl/projects/${project.name}/models`,
      },
      {
        name: "select datasets",
        link: `#`,
      },
    ];
  } else if (link.includes("newmodel")) {
    paths = [
      { name: "projects", link: "/beyond-data/automl/projects/" },
      {
        name: "models",
        link: `/beyond-data/automl/projects/${project.name}/models`,
      },
      {
        name: "newmodel",
        link: `#`,
      },
    ];
  } else if (link.includes("models")) {
    paths = [
      { name: "projects", link: "/beyond-data/automl/projects/" },
      {
        name: "models",
        link: `#`,
      },
    ];
  } else if (link.includes("newproject")) {
    paths = [{ name: "projects", link: "/beyond-data/automl/projects/" }];
  } else if (link.includes("projects")) {
    paths = [{ name: "home", link: "#" }];
  }

  return (
    <>
      <Breadcrumb
        style={{ marginLeft: "8em", marginTop: "10px", textAlign: "left" }}
      >
        {paths
          ? paths.map((item, index) => (
              <Breadcrumb.Item href={item.link} key={index}>
                {item.name}
              </Breadcrumb.Item>
            ))
          : null}
      </Breadcrumb>
    </>
  );
}
